@media(max-width: 640px){.card-chat{width:75% !important}}@media(max-width: 1000px){.card-chat{width:50% !important}}.card-chat{background:#fff !important}.chat-area{width:100%;height:100%}.chat-button{border-radius:"100%";position:"fixed";bottom:"50px";right:"20px";min-width:"5rem";background:#45b2db}.chat-bubble .no-content-message{height:110% !important}.chat-app{width:100%;height:100%;display:flex}.chat-app .no-content-message{color:#ccc;font-style:italic;font-size:20px;text-align:center;margin:20px;height:22.8rem}.chat-app .channel-list{width:calc(20% - 12px);border:1px solid #e0e0e0;margin:10px;margin-right:0;border-right:none;height:calc(100% - 22px)}.chat-app .channel-item{border-bottom:1px solid #e0e0e0;padding:10px}.chat-app .channel-item div{font-weight:bold}.chat-app .channel-item span{font-size:10px}.chat-app .channel-item:hover{background-color:#e0e0e0;cursor:pointer}.chat-app .messages-panel{width:100%;border:1px solid #e0e0e0;height:98%;display:flex;flex-direction:column;align-items:flex-start}.chat-app .messages-panel .messages-list{align-self:stretch;height:100%}.chat-app .messages-panel .messages-list .message-item{border-radius:5px;margin-left:10px;margin-right:10px}.chat-app .messages-panel .messages-list .message-item .images{display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));grid-gap:15px;list-style:none;margin:0;padding:0}.chat-app .messages-panel .messages-list .message-item ul.images>li{position:relative;padding-top:18%}.chat-app .messages-panel .messages-list .message-item ul.images>li .fullscreen-container{position:absolute;top:0;left:0;right:0}.chat-app .messages-panel .messages-list .message-item ul.images>li .fullscreen-container button.fullscreen-btn{right:auto}.chat-app .messages-panel .messages-list .message-item ul.images>li .fullscreen-container button.fullscreen-btn div.fullscreen-image{padding-top:2rem;bottom:0}.chat-app .messages-panel .messages-list .message-item ul.images>li img{position:absolute;top:0;left:0;right:0;bottom:0;height:90% !important;width:100% !important;object-fit:cover}@media(max-width: 567px){.chat-app .messages-panel .messages-list .message-item ul.images{grid-template-columns:repeat(auto-fit, minmax(150px, 1fr))}}.chat-app .messages-panel .messages-input{width:100%;height:90px;margin-top:2px;padding-top:.4rem;padding-bottom:4.1rem;border-top:1px solid #e0e0e0;background-color:#f0f0f0;display:flow-root;align-items:baseline}.chat-app .messages-panel .messages-input input{margin:auto;height:20px;width:90%;margin-left:15px;border-radius:15px;border:1px solid #e0e0e0}.chat-app .messages-panel .messages-input input:focus{border-radius:15px;border:2px solid #66a6ff;outline:none}.chat-app .messages-panel .messages-input button{margin:auto 10px;background-color:#0e62da;color:#fff;border:1px solid;border-radius:10px;padding:5px 13px}.chat-app .messages-panel .messages-input button:hover{cursor:pointer;background-color:#66a6ff}.chat-app .messages-panel .messages-input .voice-recorder{display:grid;place-content:center}.chat-app .messages-panel .messages-input .recorder-container{margin-top:-5%;border-radius:1rem}